.navbar__link {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  text-decoration: none;
  border: 0;
  background: transparent;
  color: #fff;
}

@media (min-width: 320px) and (max-width: 1279px) {
  .navbar__link {
    font-size: inherit;
  }
}